.header__login-button {
    width: auto;
    height: 22px;
    margin: 0px 0px 0px 24px;
    padding: 0;
    background-color: transparent;
    color: white;
    border: none;
    cursor: pointer;
    transition: .3s ease;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
}

.header__login-button:hover {
    opacity: 0.6;
}
