.element__delete-button {
  width: 18px;
  height: 19px;
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
  border: none;
  padding: 0;
  background-image: url(../../../images/delete.svg);
  background-repeat: no-repeat;
  transition: opacity 0.3s linear;
}

.element__delete-button:hover {
  opacity: 0.6;
  cursor: pointer;
}
